/* 选项对应的数据 */
.type_switch_icon {
  margin-left: 20px;
}
.type_switch_icon > span {
  margin-right: 5px;
}
/* 可拖拽内容 */
.Simple-1 {
  border: 1px solid #E9E9E9;
  border-radius: 4px;
  width: 100%;
  padding: 5px;
  max-height: 420px;
  overflow-y: auto;
  overflow-x: visible;
}
.Simple-1 .DraggableTags-tag {
  width: 100%;
}
.Simple-1 .DraggableTags-tag .row {
  position: relative;
  margin: 5px;
  font-size: 13px;
  border: 1px dashed #3b9de9;
  border-radius: 4px;
  padding: 5px;
  line-height: 30px;
  color: #666666;
  background: rgba(255, 255, 255, 0.7);
}
.Simple-2 {
  border: 1px solid #E9E9E9;
  border-radius: 4px;
  width: 100%;
  padding: 5px;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: visible;
}
.Simple-2 .DraggableTags-tag {
  width: 150px;
}
.Simple-2 .DraggableTags-tag .row {
  position: relative;
  margin: 5px;
  font-size: 13px;
  border: 1px dashed #3b9de9;
  border-radius: 4px;
  padding: 5px;
  line-height: 30px;
  color: #666666;
  background: rgba(255, 255, 255, 0.7);
}
.tag {
  margin: 3px;
  font-size: 13px;
  border: 1px dashed #91d5ff;
  border-radius: 4px;
  padding: 0 8px;
  background: #e6f7ff !important;
  line-height: 30px;
  color: #52acff;
  background: rgba(255, 255, 255, 0.7);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.delete {
  position: absolute;
  top: 3px;
  right: 3px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.case_must_checkbox {
  margin-left: 20px;
}
.case_must_input {
  width: 200px;
}
.case_children_lv1_rangNumber {
  margin-left: 180px;
}
.case_children_lv1_choiseContent {
  padding-left: 165px;
  margin-top: 8px;
}
.case_children_lv1_choiseContent .choise_operate {
  margin-left: 20px;
  color: #6236FF;
  cursor: pointer;
}
